<template>
    <div class="Home">
        <Carousel :type="'home'" :num="6"></Carousel>
        <div class="img_list">
            <div class="img_list_top">
                <span>艺 苑 宾 客</span>
                <span class="know">了解更多</span>
            </div>
            <div class="img_box clearfloat">
                <div v-for="(car,index) in list" :key="index">
                  <img  :src="car.img">
                  <div class="text-bottom">{{car.name}}</div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
import Carousel from '@/components/Carousel'
export default {
  name: 'Home',
  components: {
    Carousel
  },
  data () {
    return {
       list:[]
    }
  },
  methods:{
      pushList(){
          var name = ['黄苗子、许麟庐、许化迟','许麟庐 黄苗子、许化迟王世襄',
          '黄苗子、王世襄、丁聪、黄永玉、许化迟等','刘海粟、许麟庐、许化迟',
          '许化迟与启功先生','许化迟与王世襄先生']
          for(var i = 0;i<6;i++){
              this.list.push(
                  {
                      img:require('../assets/home/'+(i+3)+'-y.jpg'),
                      name:name[i]
                  }
              )
          }
      }
  },
  mounted(){
     this.pushList();
  }
}
</script>

<style lang="less" scoped>
  .carousel{
      width: 1170px;
      margin: 0 auto;
      background: #000000;
      margin-bottom: 20px;
  }
  .img_list{
      width: 1170px;
      margin: 0 auto;
  }
  .img_list_top{
      padding: 12px 0;
      text-align: center;
      font-size: 20px;
    color: #79242F;
       border-top: 2px solid  #79242F;
    border-bottom: 2px solid  #79242F;
    margin-bottom: 20px;
     position: relative;
    .know{
        position: absolute;
        right: 0;
        top:18px;
        font-size: 10px;
        color: #79242F;
    }
  }
  .img_box{
      div{
          width: 380px;
          float: left;
          margin-bottom: 10px;
          margin-right: 15px;
          img{
              width: 100%;
               height: 300px;
               display: block;
          }
      }
      div:nth-child(3n){
          margin-right: 0;
      }
      .text-bottom{
          height: 40px;
         background: #EFCAA9;
         line-height: 40px;
         text-align: center;
         font-size: 18px;
        color: #79242F;
      }
  }
  .el-carousel__item{
      img{
          width: 100%;
          height: 100%;
      }
  }
  
</style>


